import axios from 'axios';

// ajax 基础数据
import { HOST, commonParam } from './config';

// 获取产品名称
export function getProductName(option) {
  const url = HOST + '/weixin/product/lists';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}

// 获取产品列表
export function getProductList(option) {
  const url = HOST + '/weixin/product/investLists';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}

// 获取产品详情
export function getProductDetail(option) {
  const url = HOST + '/weixin/product/detail';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}

// 获取产品历史报告
export function getProductReport(option) {
  const url = HOST + '/weixin/product/reportLists';

  const data = Object.assign({}, commonParam, option);

  return axios.post(url, data);
}



