<template>
  <div class="product-detail hide">
    <div class="header">
      <div class="invest-sum">
        <span>投资金额：</span>
        <span class="currency">{{detail.money}}</span>
      </div>
      <span class="link" @click="toReport">查看报告</span>
    </div>
    <div class="section">
      <ul>
        <li>
          <span>产品状态：</span>
          <span>{{statusText[detail.status]}}</span>
        </li>
        <li>
          <span>成立日期：</span>
          <span>{{detail.establish}}</span>
        </li>
        <li>
          <span>管理人：</span>
          <span>{{detail.manager}}</span>
        </li>
        <li>
          <span>产品期限：</span>
          <span>{{detail.deadline}}</span>
        </li>
        <li>
          <span>管理机构：</span>
          <span>{{detail.trusteeship}}</span>
        </li>
        <li>
          <span>投资范围属性：</span>
          <span>{{detail.scope}}</span>
        </li>
      </ul>
    </div>
    <div class="section">
      <ul>
        <li>
          <span>认购费：</span>
          <span class="currency">{{detail.subscription_fee}}</span>
        </li>
        <li>
          <span>赎回费：</span>
          <span class="currency">{{detail.redemption_fee}}</span>
        </li>
        <li>
          <span>管理费：</span>
          <span class="currency">{{detail.management_fee}}</span>
        </li>
        <li>
          <span>托管费：</span>
          <span class="currency">{{detail.trust_fee}}</span>
        </li>
        <li>
          <span>外包服务费：</span>
          <span class="currency">{{detail.outsourcing_fee}}</span>
        </li>
      </ul>
    </div>
    <transition name="slide-left">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
import { getProductDetail } from '@/api/product';
import { AID, ERR_OK } from '@/api/config';

import { PRODUCT_STATE_TEXT } from '@/common/js/config';

export default {
  data() {
    return {
      detail: {}
    };
  },
  created() {
    this.statusText = PRODUCT_STATE_TEXT;

    this._getDetail();
  },
  methods: {
    _getDetail() {
      const id = this.$route.params.id;

      getProductDetail({ id }).then((res) => {
        if (res.data.code === ERR_OK) {
          this.detail = res.data.data.info;
        } else {
          this.$dialog.alert({
            mes: res.data.msg,
            callback() {}
          });
        }
      }).catch((err) => {
        console.log(err);
      });
    },
    toReport() {
      const id = this.$route.params.id;

      this.$router.push(`/product/${id}/report/history/aid/${AID}`);
    }
  }
};
</script>

<style scoped lang="stylus">
@import "~@/common/stylus/variable";
@import "~@/common/stylus/mixin";

animation-slide-left(0.4s)
.product-detail
  /* 顶部信息 */
  .header
    display flex
    justify-content space-between
    height 0.8rem
    padding 0 0.3rem
    margin 0.26rem 0 0.17rem 0
    background-color #fff
    box-shadow 0 3px 5px 1px #ddd
    line-height 0.8rem
    font-size 0.34rem
    .invest-sum
      .currency
        color $color-main
    .link
      text-decoration underline
      font-size 0.3rem
      color $color-link
  /* 详细信息 */
  .section
    padding 0.25rem
    margin-bottom 0.17rem
    background-color #fff
    box-shadow 0 3px 5px 1px #ddd
    ul>li
      display flex
      margin-bottom 0.3rem
      &:last-child
        margin-bottom 0
      span
        font-size 0.32rem
        word-break break-all
        &:first-child
          flex-shrink 0
          flex-basis 2.5rem
          text-align right
</style>



